.RandomUser {
    width: 75%;
    min-height: 55%;
    padding: 2.5%;
    background-color: #A07676;
    color: #C6FCED;
}

.UpperSection {
    display: flex;
    justify-content: left;
    width: 100%;
    min-height: 40%;
}

.UserImage {
    width: 8rem;
    height: 8rem;
    object-fit: cover;
    border-radius: 50%;
}

.NameAndEmail {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 60%;
    margin-left: 3rem;
}
.Name {
    font-size: 2rem;
    font-weight: 500;
}
.Email {
    font-size: 1.3rem;
    font-weight: 500;
}


.LowerSection{
    width: 40%;
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: top;
    align-items: left;
    padding-top:1rem;
}
.InfoContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 0.5rem;
}


@media(max-width:700px){
    .RandomUser{
      height: 70%;
    }
    .UpperSection{
        flex-direction: column;
        align-items: center;
    }
    .NameAndEmail{
        margin-left: 0;
        margin-top:1rem;
        align-items: center;
    }
    .Name{
        font-size: 1.3rem;
    }
    .Email{
        font-size: 1rem;
    }
    .LowerSection{
        width: 100%;
        align-items: center;
    }

    .InfoContainer{
        flex-direction: column;
    }
  }