.Button {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 3rem;
    width: 8rem;
    background-color: #8AD7C1;
    color: #fff;
    cursor: pointer;
    transition: 0.5s ease;
}

.Button:hover{
    background-color: #fff;
    color: #8AD7C1;
}

.ButtonText {
    font-size: 1.2rem;
    user-select: none;
}