.App{
  width: 100vw;
  height: 90vh;
  padding: 5vh 0;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: #825959;
}

.Title {
  color: #8AD7C1;
  font-size: 3rem;
  width: 90%;
  text-align: center;
}

@media(max-width:700px){
  .Title{
    font-size: 1.5rem;
  }
}